.nav {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 1.5em;
}

.nav__logo {
	font-size: 1.5rem;
	font-weight: bold;
	color: var(--clr-primary);
	margin-left: 1.5em;
	text-decoration: none;
}

.nav__list {
	display: flex;
	margin-right: 1.5em;
}

.nav__list-item {
	margin-left: 1.5em;
}

.nav__contact {
	font-size: 1rem;
	color: var(--clr-primary);
	text-decoration: none;
	margin-right: auto;
}

.app .nav__hamburger {
	display: none;
}

.nav__theme {
	margin-top: 0;
	display: flex;
	align-items: center;
}

@media (max-width: 600px) {
	.nav__list {
		display: none;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: fixed;
		inset: 0;
		width: 100%;
		height: 100%;
		z-index: 2;
	}

	.nav__list-item {
		margin: 0.5em 0;
	}

	.app .nav__hamburger {
		display: flex;
		z-index: 2;
		margin-left: 0.8em;
	}
}